[data-theme=gastly] {
    --color__background: #1b0b36;
    --color__background__alt: #271643;
    --color__background__widget: #3b295a;
    --color__background__contrast: #141414;
    --color__background__contrast__widget: #262626;
    --color__background__contrast__alt: #333333;
    --color__background__gradient: linear-gradient(180deg,rgba(0,0,0,0) 0,#141414 100%);
    --color__overlay: rgba(0,0,0,.5);
    --color__link: #9090f5;
    --color__link__visited: #95799F;
    --color__text: #{$color__95-brightness};
    --color__text-alt: #{$color__65-brightness};
    --color__text__contrast: white;
    --color__text__contrast__alt: #a9a9a9;
}
